.hero__slides {
  z-index: 4;
  width: 100%;
  height: 100%;
  position: relative;

  .theme-citynet & {
    height: 198px;
  }

  @include respond-to('m') {
    > .row {
      padding: 5em 0 12em;
    }
  }

  .flex-control-nav,
  .flex-direction-nav {
    display: none;
  }

  .custom-navigation {

    a {
      -ms-transform: none;
      -webkit-transform: none;
      transform: none;
    }

    @include respond-to('l') {
      position: absolute;
      right: 0;
      bottom: 0;

      a {
        position: relative;
      }
    }

  }
}

.hero__slides--img {
  position: absolute;
  top: 0;
  z-index: 0;
  width: 100%;
  height: 100% !important;

  li {
    &:after {
      content: '';
      z-index: 5;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      //background: rgba(0, 0, 0, 0.35);
    }

    @include respond-to('m') {
      &:after {
        display: none;
      }
    }
  }

  .slides,
  li,
  .hero__bg {
    height: 100%;
  }
}

.hero--credit {
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 10;

  p {
    margin: 0;
    color: white;
    padding: .5em 1em;
    font-style: normal;
    display: inline-block;
    background: rgba(0, 0, 0, 0.45);
    @include rem('font-size', 11px);

    .hero-image-by-prefix,
    .hero-image-by {
      @include rem('font-size', 10px);
    }

    @include respond-to('m') {
      padding: .5em 3.5em .5em 1em;
    }
  }

  @include respond-to('m') {
    right: 0;
    bottom: 8em;
    text-align: right;
  }
}

.hero__content {
  z-index: 4;
  color: white;
  margin: 0 0 3em;
  padding: 1.5em;
  position: relative;

  h1 {
    margin: 0;
    color: white;
    @include rem('font-size', 45px);
  }

  p {
    margin: 0;
    @include rem('font-size', 18px);
  }

  .btn {
    display: block;
    margin-top: 2em;
    font-weight: 600;
    padding: 1.25em 2.5em;
  }

  // @include mq($from: mobile-landscape) {
