/* ============================================================
  Styles for the header.
  ============================================================ */

.off-canvas-wrapper {

  .off-canvas-main-menu {
    background: $off-black;
    top: auto;

    ul {
      margin: 0;

      a {
        border-bottom: 1px solid $spruce;
        color: $white;
        display: block;
        padding: 1rem;
      }
    }
  }
}

.header {
  margin-bottom: 1.5rem;

  @include respond-to('m') {
    display: block;
    margin-bottom: 0;
  }

  .header-first,
  .header-second {
    background-color: $white;

    @include respond-to('m') {
      background: $cerulean;
    }
  }

  .header-first {
    padding: 0.5rem 0 1rem;

    @include respond-to('m') {
      padding: 1rem 0;
    }

    .button {
      margin-bottom: 0;

      &::after {
        background: $cerulean;
        box-shadow: 0 7px 0 $cerulean, 0 14px 0 $cerulean;
      }
    }

    .burger-wrapper {
      $s: 0.2rem;

      cursor: pointer;
      height: $s * 8;
      margin: auto;
      width: $s * 10;

      .hamburger {
        background: $cerulean;
        height: $s;
        position: relative;
        transition: background 10ms 300ms ease;
        transform: translateY($s * 4);
        width: $s * 10;

        &:before,
        &:after {
          background: $cerulean;
          content: '';
          height: $s;
          left: 0;
          position: absolute;
          transition: top 300ms 350ms ease,
          transform 300ms 50ms ease;
          width: $s*10;
        }

        &:before {
          top: $s * -3;
        }

        &:after {
          top: $s * 3;
        }
      }
    }

    // .menu-trigger:checked {
    //
    //   ~ .burger-wrapper {
    //
    //     .hamburger {
    //       background: transparent;
    //
    //       &:after, &:before {
    //         top: 0;
    //         transition: top 300ms 50ms ease,
    //         transform 300ms 350ms ease;
    //       }
    //
    //       &:before {
    //         transform: rotate(45deg);
    //       }
    //
    //       &:after {
    //         transform: rotate(-45deg);
    //       }
    //     }
    //   }
    // }

    .logo-mobile {
      width: 4.5rem;
    }

  }

  .header-second {
    background-color: $off-white;
    padding-left: 0.625rem;
    padding-right: 0.625rem;

    @include respond-to('m') {
      background-color: $white;
      padding: 0;
    }

    .main-menu {

      @include respond-to('m') {
        background: $white;
        display: block;
        padding: 0;
      }

      .full-width {
        width: 100%;
      }
    }

    .tbm-submenu-toggle,
    .tbm .tbm-item {
      background-color: $white;
    }

    .tbm-submenu-toggle {
      color: $tulip;
    }

    .tbm-submenu-toggle:hover,
    .tbm-submenu-toggle:focus,
    .tbm .tbm-item a:hover,
    .tbm .tbm-item a:focus,
    .tbm .tbm-item--has-dropdown a:hover:before,
    .tbm .tbm-item--has-dropdown a:focus:before,
    .tbm .tbm-item .open {
      background-color: $tulip;
      color: $white;
      text-decoration: none;
    }

    .tbm .nav li.dropdown.active > .dropdown-toggle .caret {
      border-top-color: $tulip;
      border-bottom-color: $tulip;
    }

    .tbm-main {

      @include respond-to('m') {
        background: $white;
      }

      a {
        background-color: $white;
        color: $spruce;

        &.tbm-link {
          font-size: .8125rem;
          font-weight: bold;
          position: relative;
          text-transform: uppercase;
        }

        &[title]:after {
          content: attr(title);
          font-size: 0.6875rem;
          font-weight: normal;
          text-transform: none;
        }
      }

      .tbm-item--has-dropdown a:before {
        color: $tulip;
        content: "\2304";
        font-size: 1.25rem;
        position: absolute;
        right: 3%;
        top: 26%;
      }

      .tbm-nav {
        justify-content: space-between;

        .tbm-item {
          flex-grow: 1;

          &.level-1:first-child {
            border-left: 1px solid rgba(0,0,0,0.2);
          }
        }
      }
    }

    .tbm-submenu {
      border-color: transparent;

      h4 {
        font-size: 1.125rem;

          a {
            text-decoration: underline;

          &:hover {
            text-decoration: none;
          }
        }
      }

      &.resident-resources h4 {
        margin: 0;
        padding: 1.125rem 0;
      }

      ul {
        margin: 0;

        li {
          font-size: 0.875rem;
          list-style: none;
          text-transform: uppercase;
        }
      }

      p {
        margin: 0;
      }
    }

    .tbm-button {
      margin: 0;
    }

    .border {
      border-bottom: 1px dashed $alto;
      margin-bottom: 0.5em;
      padding-bottom: 0.5em;
    }

    .heading--snug {
      padding-right: 1rem;
    }


    .nav__event {
      margin: 0 1rem 1rem 0;

      > div {
        padding-left: 1rem;
      }

      a {
        color: $cerulean;
      }
    }
  }

  .search-bar {

    @include respond-to('m') {
      display: block;
    }

    .form-submit {
      background-image: url(../images/search-icon-blue.png);
      width: 2rem;
    }

    .input-group {
      margin: 0;
    }

    input {
      font-size: 0.825rem;

      &::placeholder {
        color: $spruce;
      }
    }
  }
}
